.works
{
    /*grid-area: w;*/
    /*grid-gap: 10px;*/
    /*margin: 10px 10% 10px 10%;*/
    display: flex;
    align-items: center;
    flex-direction: column;

    min-height: 80vh;
    padding: 40px;
}

.container
{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-evenly;

    width: 90%;
    /*background-color: rgb(35%, 35%, 35%);*/
    min-height: 80vh;

    color: lightgreen;
}

.wrap
{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
}

.title
{
    font-size: 24px;

    margin: 30px;

    color: lightgreen;
}

.block
{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-around;

    height: 450px;
    margin: 40px;

    /*border: 2px solid;*/
    /*border-color: grey;*/
}

.image
{
    display: flex;
    align-items: center;
    /*min-width: 350px;*/
    justify-content: center;

    width: 300px;
    height: 300px;

    background-image: url(https://uploads.dev.by/resources/1ab307c0-7633-4f42-90b4-2448e53a9802/optimized/9626f87cb4.jpeg);
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: cover;
}

.buttonClick
{
    font-size: 10pt;

    width: 80px;
    height: 30px;
    margin: 15px;

    color: purple;
    border-radius: 10px;
    background-color: lightgreen;
}

.description
{
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;

  width: 320px;
  /*border: 1px solid;*/
  height: 100px;
  margin: 5px;
}

@media screen and (max-width: 1200px)
{
    .container
    {
        justify-content: center;
    }
}
